<template>
	<div id="gameWrap" ref="content">
		<div class="listWrap">
			<dl class="gameList">
				<dt>当前选择:{{this.curs}}</dt>
				<dt class="hot">热门游戏：</dt>
				<dd>
					<span v-for="cur of hot" class="border" @click="gameChoose(cur.game,cur.id)">{{cur.game}}</span>
				</dd>
				<template v-for="(item,key) of game">
					<dt :ref="key">{{key}}</dt>
					<dd v-for="(innerItem,index) of item" @click="gameChoose(innerItem.game,innerItem.id)">
						{{innerItem.game}}
					</dd>
				</template>
			</dl>
		</div>
		<ul class="letterIndex">
			<li v-for="(item,key) of game" @click="scrollToView(key)" ref="index">{{key}}</li>
<!-- 			<li v-for="(item,key) of city" @click="scrollToView(key)"
			 @touchstart.prevent="touchstartHander"  @touchmove="touchmoveHander"  @touchend="touchendHander" :ref="'index'+key">{{key}}</li> -->
		</ul>
		<span id="bigTip">A</span>
	</div>
</template>
<script type="text/javascript">
	import axios from "axios";
	import {mapState,mapMutations} from "vuex";
	import "@/assets/css/border.css";
	export default{
		props:{
			game:Object,
			hot:Array
		},
		data (){
			return {
				touchStatus:false,
				timer:null
			}
		},
		computed:{
			letters (){
				const letters=[]
				for(let i in this.game){
					letters.push(i);
				}
				return letters
			},
			...mapState({
				curs:'curSelect'
			})
		},
		updated (){
			this.offsetT=this.$refs['index'][0].offsetTop+20;
		},
		methods:{
			scrollToView (letter){
				document.getElementById('bigTip').classList.add("showLetter");
				document.getElementById('bigTip').innerHTML=letter
				this.$refs[letter][0].scrollIntoView();
				setTimeout(function() {
					document.getElementById('bigTip').classList.remove("showLetter");
				},500)
			},
			touchstartHander (){
				this.touchStatus=true;
			},
			...mapMutations({myGameChange:'myGameChange'}),
			gameChoose (game,id){
				// this.$store.dispatch("gameChoose",{"name":game,"id":id})
				// this.$store.commit("myGameChange",game)
				var type=this.$route.query.will;
				if(type=="Rent"){		
					this.myGameChange({"name":game,"id":id});
					this.$router.push("/"+type)
				}else if(type=="Publish"){
					//如果这里是push的话那么多次选择时会有很多/Publish被保存下来
					//replace覆盖掉本次的历史纪录，他就好像没有出现过，被将要跳转的地址
					//replace掉了
					this.bus.$emit("mypublish",game,id)
					this.$router.replace("/"+type)
				}
			},
			touchmoveHander (e){
				if(this.touchStatus){
					if(this.timer){
						clearTimeout(this.timer)
					}
					this.timer=setTimeout(()=> {
						var clientY=e.changedTouches[0].clientY;
						var index=Math.floor((clientY-this.offsetT)/21);
						if(index>=0&&index<this.letters.length)
						this.scrollToView(this.letters[index])
					}, 20);
				}
			},
			touchendHander (){
				this.touchStatus=false;
			}
		}
	}
</script>
<style scoped>

@import "../../assets/css/gameChoose.css";
 .border::before{
	border-color: #e6292c;
 }
 @media (-webkit-min-device-pixel-ratio:1.5) and (-webkit-max-device-pixel-ratio:2.5){
 	.border::before{
 		border-radius: 20px;
 	}
 }
 @media screen and (-webkit-min-device-pixel-ratio:2.5){
 	.border::before{
 		border-radius: 20px;
 	}
 }
</style>


